import React, { useState, useEffect } from "react";

import Grid from "@mui/material/Grid"; // Grid version 1
import TextField from "@mui/material/TextField";
import { Button, Checkbox, CircularProgress,  Box } from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import Paper from "@mui/material/Paper";
import { styled } from "@mui/material/styles";
import axios from "axios";
import "../style.css";

const Item = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(1),
  textAlign: "center",
}));

const SearchApi = () => {
  const [tokenApi, setTokenApi] = useState({});

  useEffect(() => {
    /* global spHelper */
    const token = spHelper.getToken();
    setTokenApi(JSON.parse(token));
    document.title = `Galor Plugin with ${spHelper.getVersion()}`;
  }, []);

  const [contact, setContact] = useState({
    index: "",
    firstname: "",
    lastname: "",
    phone: "",
    email: "",
    personalid: "",
  });

  const [searchResult, setSearchResult] = useState([]);
  const [isSearching, setIsSearching] = useState(false);
  const [selectedResult, setSelectedResult] = useState([]);
  const handleSearch = async () => {
    setIsSearching(true);
    const searchUrl =
      "https://tbapi-sandbox.travelbooster.com/tbapi/v1/Customer/FindCustomers";
    await axios
      .request({
        method: "POST",
        url: searchUrl,
        headers: {
          Authorization: `Bearer ${tokenApi.access_token}`,
          Accept: "application/json",
        },
        data: {
          Index: contact.index,
          FirstName: contact.firstname,
          LastName: contact.lastname,
          Phone: contact.phone,
          Email: contact.email,
          PersonalId: contact.personalid,
        },
      })
      .then(function (response) {
        // handle success
        setSearchResult(response.data);
        console.log(response);
      })
      .catch(function (error) {
        setSearchResult([]);
        if (error.response.status === 429) {
          alert("Galor API is on cant handle this many request yet");
        }
        // handle error
        console.log(error);
      })
      .finally(function () {
        setIsSearching(false);
      });
  };

  const onImport = async (e) => {
    console.log(selectedResult.length);
    let command = "";
    for (let i = 0; i < selectedResult.length; i++) {
      const matched = searchResult.find((y) => y.Index === selectedResult[i]);
      command +=
        "N." + matched.LastName + "/" + matched.FirstName + matched.Title;
      if (matched.IsPhoneSelected && matched.PhonesList.length > 0) {
        for (let j = 0; j < matched.PhonesList.length; j++) {
          command += "+P.AUTH*" + matched.PhonesList[j];
        }
      }
      if (
        matched.IsEmailSelected &&
        matched.EMail != null &&
        matched.EMail !== ""
      ) {
        let newEmail = matched.EMail;
        command +=
          "+" +
          "P.BOME*" +
          newEmail.replace("_", "--").replace("@", "//").toUpperCase();
      }

      command += "+";
    }
    command = command.slice(0, -1);
    console.log(command);
    await spHelper.sendTerminalCommand(command);
  };
  const columns = [
    { field: "Index", headerName: "ID", width: 100 },
    // { field: "UserID", headerName: "User Id", width: 130 },
    { field: "FirstName", headerName: "First Name", width: 130 },
    { field: "LastName", headerName: "Last Name", width: 130 },
    // { field: "Email", headerName: "Email Address", width: 130 },
    {
      field: "IsEmailSelected",
      headerName: "Email",
      type: "bool",
      width: 90,
      renderCell: (params) => {
        return (
          <>
            <span>{params.row.EMail}</span>
            <Checkbox
              style={{ marginLeft: 15, marginBottom: 0 }}
              onChange={(e) => {
                const selectedRow = searchResult.find(
                  (t) => t.Index === params.id
                );
                selectedRow.IsEmailSelected = !selectedRow.IsEmailSelected;
              }}
              disabled={
                !selectedResult.length ||
                !selectedResult.find((r) => r === params.id)
              }
            ></Checkbox>
          </>
        );
      },
    },
    {
      field: "IsPhoneSelected",
      headerName: "Phone",
      type: "bool",
      width: 90,
      renderCell: (params) => (
        <Checkbox
          style={{ marginLeft: 15, marginBottom: 0 }}
          onChange={(e) => {
            const selectedRow = searchResult.find((t) => t.Index === params.id);
            selectedRow.IsPhoneSelected = !selectedRow.IsPhoneSelected;
          }}
          disabled={
            !selectedResult.length ||
            !selectedResult.find((r) => r === params.id)
          }
        ></Checkbox>
      ),
    },
    {
      field: "IsPassportSelected",
      headerName: "Passport",
      type: "bool",
      width: 90,
      renderCell: (params) => (
        <Checkbox
          style={{ marginLeft: 15, marginBottom: 0 }}
          onChange={(e) => {
            const selectedRow = searchResult.find((t) => t.Index === params.id);
            selectedRow.IsPassportSelected = !selectedRow.IsPassportSelected;
          }}
          disabled={
            !selectedResult.length ||
            !selectedResult.find((r) => r === params.id)
          }
        ></Checkbox>
      ),
    },
  ];
  const searchResultSection =
    searchResult.length !== 0 && !isSearching ? (
      <>
        <div style={{ height: "400px", width: "100%" }} className="ag-theme-alpine">
          <h2>Search Result</h2>
          <DataGrid
            className="ag-theme-alpine"
            rows={searchResult}
            columns={columns}
            pageSize={5}
            rowsPerPageOptions={[5]}
            disableSelectionOnClick
            checkboxSelection
            getRowId={(row) => row?.Index}
            onSelectionModelChange={(ids) => {
              setSelectedResult(ids);
            }}
          />
        </div>{" "}
        <div>
          {selectedResult.length !== 0 ? (
            <Button
              variant="contained"
              onClick={async () => {
                await onImport();
              }}
            >
              Import To Smartpoint
            </Button>
          ) : null}
        </div>
      </>
    ) : null;
  const searching = isSearching ? <CircularProgress /> : null;
  return (
    <div>
      {Object.keys(tokenApi).length !== 0 ? (
        <div>
          <Grid>
            <Grid xs={12}>
              <Item>
                <h2>Search Traveller</h2>
              </Item>
            </Grid>
            <Grid height={60} xs={12} justifyContent="center">
              <Item>
                <TextField
                  id="outlined-required"
                  label="Index (  customers Travel Booster id )"
                  value={contact.index}
                  onChange={(e) => {
                    setContact({ ...contact, index: e.target.value });
                  }}
                />
                &nbsp;
                <TextField
                  id="outlined-required"
                  label="First Name"
                  value={contact.firstname}
                  onChange={(e) => {
                    setContact({ ...contact, firstname: e.target.value });
                  }}
                />
                &nbsp;
                <TextField
                  id="outlined-required"
                  label="Last Name"
                  value={contact.lastname}
                  onChange={(e) => {
                    setContact({ ...contact, lastname: e.target.value });
                  }}
                />
              </Item>
            </Grid>
            <Grid xs={12} justifyContent="center">
              <Item>
                <TextField
                  re
                  id="outlined-required"
                  label="Phone"
                  value={contact.phone}
                  onChange={(e) => {
                    setContact({ ...contact, phone: e.target.value });
                  }}
                />
                &nbsp;
                <TextField
                  id="outlined-required"
                  label="Email"
                  value={contact.email}
                  onChange={(e) => {
                    setContact({ ...contact, email: e.target.value });
                  }}
                />
                &nbsp;
                <TextField
                  id="outlined-required"
                  label="PersonalId"
                  value={contact.personalid}
                  onChange={(e) => {
                    setContact({ ...contact, personalid: e.target.value });
                  }}
                />
              </Item>
            </Grid>
            <Grid xs={12} justifyContent="center">
              <Item>
                <Button variant="contained" onClick={handleSearch}>
                  Search
                </Button>
              </Item>
            </Grid>
          </Grid>
        </div>
      ) :  <Box sx={{ width: '75%', margin: 'auto', marginTop: '10px' }}>
      Please wait while connecting to TravelBooker...
    </Box>}

      {searching}
      {searchResultSection}
    </div>
  );
};

export default SearchApi;
